.fol-graphexplorer-cYTZ7LnVXZ body {
  user-select: none;
  /*fix the bug, when text is dragged over canvas and cause loading of different element (text - element)*/
}
.fol-graphexplorer-cYTZ7LnVXZ .btn-graph {
  outline: none;
  color: black;
  text-align: center;
  font-size: 13px;
  padding: 0 0.3em;
  font-weight: bold;
  border: 0;
}
.fol-graphexplorer-cYTZ7LnVXZ .teacher-mode:hover {
  background-color: #28a745;
}
.fol-graphexplorer-cYTZ7LnVXZ .split-pane {
  height: calc(100vh - 64px) !important;
  /* cancel-out container-fluid's padding so that the resizer spans
       to viewport edges */
  margin-left: -15px;
  margin-right: -15px;
}
.fol-graphexplorer-cYTZ7LnVXZ .vh-pane-left,
.fol-graphexplorer-cYTZ7LnVXZ .vh-pane-right {
  height: calc(100vh - 64px);
  padding: 0 15px;
}
.fol-graphexplorer-cYTZ7LnVXZ [data-type="Resizer"] {
  width: 12px !important;
}
@media all and (max-width: 991.9px) {
  .fol-graphexplorer-cYTZ7LnVXZ .vh-pane-left,
  .fol-graphexplorer-cYTZ7LnVXZ .vh-pane-right {
    height: 100%;
  }
  .fol-graphexplorer-cYTZ7LnVXZ .vh-pane-right {
    padding-top: 15px;
    min-height: 100%;
  }
  .fol-graphexplorer-cYTZ7LnVXZ [data-type="Resizer"] {
    width: 100% !important;
    height: 12px !important;
  }
}
@media all and (max-width: 991.9px) and (max-height: 240px) {
  .fol-graphexplorer-cYTZ7LnVXZ .split-pane {
    height: auto !important;
  }
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .split-pane,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .vh-pane-left,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .vh-pane-right {
  height: auto !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .btn {
  display: none;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .form-control,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .custom-select,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .input-group-prepend,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .input-group-append,
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .input-group-text {
  border: none;
  background: initial;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  padding-left: 0 !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .input-group.mb-1 {
  margin-bottom: 0 !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .form-control {
  height: auto !important;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .form-group {
  margin-bottom: 0.5rem;
}
.fol-graphexplorer-cYTZ7LnVXZ .view-mode .form-label {
  margin-bottom: 0.25rem;
}
